import React from 'react';
import PropTypes from 'prop-types';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from './Home.css';
import adaptUrl from './adapt.png';
import innoUrl from './inno.png';
import resUrl from './res.png';


class Home extends React.Component {
  static propTypes = {
    news: PropTypes.arrayOf(PropTypes.shape({
      title: PropTypes.string.isRequired,
      link: PropTypes.string.isRequired,
      content: PropTypes.string,
    })).isRequired,
  };


  render() {
    return (
      <div className={s.root}>
        <div className={s.container}>
          <h1>Responsive</h1>
          <img src={resUrl} width="300" height="200" alt="responsive" />
            <p>Cross platform design</p>
          <h1>Innovative</h1>
          <img src={innoUrl} width="300" height="200" alt="innovative" />
            <p>Leading the web with latest and greatest implementation</p>
          <h1>Adaptive</h1>
          <img src={adaptUrl} width="300" height="200" alt="adapt" />
            <p>Continuously grow and change with the fast moving Web enviroment</p>
        </div>
      </div>
    );
  }
}

export default withStyles(s)(Home);



